import classNames from 'classnames';

//主组件
const BackgroundTransitions = (props) => {
	const prefixCls = 'ani-bg';
	const {
		className,
		type = 'grow',
	} = props;
	const classString = classNames(
		className,
		prefixCls,
		`${prefixCls}-${type}`,
	);
	return <span className={classString}>
		{props.children}
	</span>;
}

export default BackgroundTransitions
